import React, { useEffect, useState } from 'react';
import { createApolloFetch } from 'apollo-fetch';
import Layout from '../layout';
import { useParams } from 'react-router-dom';
import { withRouter } from 'react-router';

import JobDetailsCard from './job-details-card.component';
import NoData from '../no-data/no-data.component';

// styles
import { Skeleton } from '@material-ui/lab';
import { Row, Col } from 'react-bootstrap';

function JobDetail(props) {
    console.log(props)
    let btns = props.location?.state;
    const { id } = useParams();
    const [job, setJob] = useState();
    const fetch = () => {
        const token = JSON.parse(localStorage.getItem('jwt')).BearerTokenResponse.access_token;
        const uri = 'https://spreago.black-squares.com/graphql';
        const apolloFetch = createApolloFetch({ uri });
        apolloFetch.use(({ request, options }, next) => {
            if (!options.headers) {
                options.headers = {};  // Create the headers object if needed.
            }
            options.headers['authorization'] = `Bearer ${token}`;
            next();
        });
        return apolloFetch
    }
    const fetchJobs = () => {
        if (localStorage.jwt) {
            const apolloFetch = fetch();
            console.log(apolloFetch);
            const applicationDate = `
                 data{
                    id
                    user{
                        id
                        name
                        profile_image{url}
                    }
                    bio{
                        speciality{name}
                        video{url}
                        created_at
                    }
                    }`
            //get user data
            apolloFetch({
                query:
                    `query($id : Int!){
                        jobFindToEmployer(id: $id){
                        id
                        title
                        brif
                        job_type{
                            name
                        }
                        job_level_of_experience_type{
                            name
                        }
                        speciality{
                            name
                        }
                        user{
                            name
                        }
                        company{
                            name
                        }
                        tags{
                            name
                        }
                        questions{
                            title
                        }
                        created_at
                        updated_at
                        applications(first: 1000){
                            ${applicationDate}
                        }
                        applicationsDone(first: 1000){
                            ${applicationDate}
                        }
                        applicationsAnswered(first:1000){
                            ${applicationDate}
                        }
                        applicationsNotAnswered(first:1000){
                            ${applicationDate}
                        }
                        applicationsIsAccepted(first: 1000){
                            ${applicationDate}
                        }
                        applicationsNotAccepted(first:1000){
                            ${applicationDate}
                        }
                        }
                    }
                       `, variables: {
                    id: id
                }
            }).then(res => {
                console.log(res)
                if (res.data.jobFindToEmployer) {
                    setJob(res.data.jobFindToEmployer)
                }
                else {
                    setJob("not found")
                }
            }).catch((err) => console.log(err));
        }
    }
    const handleApplicationAnswer = (id , is_accepted , comment) => {
        if (localStorage.jwt) {
            const apolloFetch = fetch();
            //get user data
            apolloFetch({
                query:
                    `mutation($application_id : Int  $is_accepted :Boolean $comment : Text){
                      applicationAnswer(Inputs: {
                        application : {id : $application_id}
                        is_accepted : $is_accepted
                        comment : $comment
                      }){
                        Status{
                          code
                          message
                        }
                        Application{
                          id
                          is_accepted
                          comment
                        }
                      }
                    }`,
                variables: {
                    "application_id" : id,
                    "is_accepted": is_accepted,
                    "comment" : comment
                }
            }).then(res => {
                fetchJobs();
            }).catch((err) => console.log(err));
        }
    }
    useEffect(function () {
        fetchJobs()
    }, []);
    if (!job) {
        return (
            <>
                <main className="container mt-5 mb-5">
                    <Row>
                        <Col md={8}>
                            <Skeleton variant="rect" height={500} />
                        </Col>
                        <Col md={4}>
                            <Skeleton variant="rect" height={400} />
                        </Col>
                    </Row>
                </main>
            </>
        )
    } else if (job === 'not found') {
        return (
            <React.Fragment key={id}>
                <NoData />
            </React.Fragment>
        )
    }
    else {
        return (
            <React.Fragment key={id}>
                <JobDetailsCard data={job} callBackFun={handleApplicationAnswer} btnsGroup={btns}/>
            </React.Fragment>
        )
    }
}
export default Layout(withRouter(JobDetail))