import React, { Component } from 'react';
import { withRouter } from 'react-router';

import withLayout from '../layout';
import LatestInterview from '../latest-Interviews/latest-Interviews.component';
import AddToList from '../add-to-list/add-to-list.component';
import RecommendedArticles from '../recommended-articles/recommended-articles.component';
import ExplorePremium from '../explore-premium/explore-premium-component';
import FindCandidate from '../find-candidate/find-candidate.component';
import LatestBiographies from '../latest-biographies/latest-biographies.component';
import YourBiographies from '../your-biographies/your-biographies.component';
import YourInterview from '../your-Interviews/your-Interviews.component';
import CompanyMembers from '../company-members/company-members.component';
import ProfileStrength from './profile-strength.component';
// styles
import {  Container , Row , Col  } from 'react-bootstrap';

class  Homepage extends Component{

    userType = JSON.parse(localStorage.getItem('jwt')).user.typeName;
    render(){
        if(this.userType){
        if(this.userType === 'Applicant'){
            return(
               <div>
                    <Container>
                        <Row>
                            <Col className="p-0" md={8}>
                                <YourBiographies/>
                                <LatestInterview/>
                            </Col>
                            <Col className="p-0 pt-4" md={4}>
                                <Container fluid>
                                    <AddToList/>
                                    <RecommendedArticles/>
                                </Container>
                            </Col>
                        </Row>
                    </Container>
                </div>
            );
        }
        else {
            return(
                <div>
                <Container>
                    <Row>
                        <Col className="p-0" md={8}>
                            <YourInterview/>
                            <LatestBiographies/>
                            <FindCandidate/>
                        </Col>
                        <Col className="p-0" md={4}>
                            <Container fluid>
                                <ProfileStrength />
                                <ExplorePremium/>
                                <CompanyMembers/>
                                <RecommendedArticles/>
                            </Container>
                        </Col>
                    </Row>
                </Container>
                </div>
            );
        } 
    }
    else{
        return(<div>error</div>)
    }
    }
}

export default withLayout(withRouter(Homepage));