import React, { Component } from 'react';
import {  Container , Row , Col  } from 'react-bootstrap';
import Card from '../card/card-component';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import { Link } from 'react-router-dom';

export default class  LatestBiographies extends Component{
    render(){
        return(<div>
            <Container>
                <Row className="pt-5">
                    <Col sm={6} xs={12} className="pb-3 pb-0-responsive">
                                <h5><Link className="text-dark" to='/visumes'>Your Visumes</Link></h5>
                    </Col>
                    <Col  className="text-right pb-3"  sm={{span :4 , offset : 2}} xs={12}>
                        <button 
                            className="bg-transparent border-0"
                            onClick ={e=> localStorage.setItem('updatedBio', '')}>
                             <h5><Link className="text-orange underline" to='/createVisume'>Create a Visume</Link></h5>
                            {/* <h5><Link className="text-orange" to='/createVisume'>Create a Visume<ArrowForwardIcon /></Link></h5> */}
                        </button>
                    </Col>
                    <Col md='4'>
                        <Card />
                    </Col>
                    <Col md='4'>
                        <Card />
                    </Col>
                    <Col md='4'>
                        <Card />
                    </Col>
                </Row>
            </Container>
        </div>);
    }
}