import React from 'react';
import { Container } from 'react-bootstrap';

import './pagination.style.css';

export default class Pagination extends React.Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            currentPage: null,
            pageCount: null,
        }
    }
    componentWillMount() {
        const startingPage = this.props.startingPage
            ? this.props.startingPage
            : 1;
        const data = this.props.card;
        const pageSize = this.props.pageSize;
        console.log(this.props);
        let pageCount = parseInt(data.length / pageSize);
        if (data.length % pageSize > 0) {
            pageCount++;
        }
        this.setState({
            currentPage: startingPage,
            pageCount: pageCount,
        });
    }
    componentWillReceiveProps(nextProps) {
        const startingPage = nextProps.startingPage
            ? nextProps.startingPage
            : 1;
        const data = nextProps.card;
        const pageSize = nextProps.pageSize;
        let pageCount = parseInt(data.length / pageSize);
        if (data.length % pageSize > 0) {
            pageCount++;
        }
        this.setState({
            currentPage: startingPage,
            pageCount: pageCount,
        });
    }
    setCurrentPage(num) {
        this.setState({ currentPage: num });
    }

    createControls() {
        let controls = [];
        const pageCount = this.state.pageCount;
        for (let i = 1; i <= pageCount; i++) {
            const baseClassName = 'pagination-controls__button';
            const activeClassName = i === this.state.currentPage ? `${baseClassName}--active` : '';
            controls.push(
                <div
                    className={`${baseClassName} ${activeClassName}`}
                    onClick={() => this.setCurrentPage(i)}
                >
                    {i}
                </div>
            );
        }
        return controls;
    }
    createPaginatedData() {
        console.log(this.props.card);
        const data = this.props.card; 
        const pageSize = this.props.pageSize;
        const currentPage = this.state.currentPage;
        const upperLimit = currentPage * pageSize;
        const dataSlice = data.slice((upperLimit - pageSize), upperLimit);
        return dataSlice;
    }
    render() {
        return (
            <Container>
                <div className='pagination text-center'>
                    <div className="w-100">
                        <div className='pagination-results'>
                            {React.cloneElement(this.props.children, { data: this.createPaginatedData() })}
                        </div>
                        <div className='text-center w-100 mb-5  pt-3 pb-3'>
                            <div className='pagination-controls'>
                                {this.createControls()}
                            </div>
                        </div>
                    </div>
                </div>
            </Container>
        );
    }
}
Pagination.defaultProps = {
    pageSize: 6,
    startingPage: 1
};