import React from 'react';

import LoginPage from './components/login-page/login-page.component';
import CreateAccPage from './components/create-acc-page/create-acc-page.component';
import EmailConfirmationPage from './components/email-conf-page/email-conf-page.component';
import RegistrationPage from './components/registration-page/registration-page.component';
import ForgetPasswordPage from './components/forget-password-page/forget-password-page.component';
import HomePage from './components/homepage/homepage.component';
import ResetPassword from './components/reset-password-page/reset-password-page.component';
import Auth from './Auth';
import CreateBiography from './components/create-biography/create-biography.component';
import Biographies from './components/biographies/biographies.component';
import ApplicantProfile from './components/applicant-profile/applicant.profile.component';
import AppliedAndSavedJobs from './components/applied-saved-jobs/applied-saved-jobs.component';
import CompanyProfileReg from './components/company-profile/company-profile-reg.component';
import CompanyProfile from './components/company-profile/company-profile.component';
import CreateJob from './components/jobs/create-job.component';
import Jobs from './components/jobs/jobs.component';
import SearchResults from './components/search/search-results.component';
import ApplyJob from './components/job-apply/apply-job.component';
import JobDetails from './components/jobs/job-details.component';
import ApplicantProfileForCompany from './components/company/applicant-profile-for-company/applicant-profile.component'

import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import 'bootstrap/dist/css/bootstrap.min.css';



import {
   //   BrowserRouter as Router,
   Switch,
   Route,
   HashRouter
} from "react-router-dom";

import './App.css';



const theme = createMuiTheme({
   palette: {
      primary: { main: '#E85A19', light: '#fff', dark: '#ea5f11' },
      secondary: { main: '#fff', light: '#000', dark: '#f1eeee' },
      contrastText: { main: '#E1631C' },
   },
   typography: {
      fontSize: 13,
      button: {
         textTransform: 'none',
         fontSize: 10,
      }
   }
});

function App() {

   return (
      <ThemeProvider theme={theme}>
         <HashRouter>
            <Switch>
               {/* <Route key="header" path="/header" component/> */}
               <Route exact path="/">
                  <LoginPage />
               </Route>
               <Route exact path="/createAccount">
                  <CreateAccPage />
               </Route>
               <Route exact path="/emailConfirmation">
                  <EmailConfirmationPage />
               </Route>
               <Route exact path="/registration">
                  <RegistrationPage />
               </Route>
               <Route exact path="/forgetPassword">
                  <ForgetPasswordPage />
               </Route>
               <Route exact path="/resetPassword">
                  <ResetPassword />
               </Route>
               <Route exact path="/companyProfileReg">
                  <CompanyProfileReg />
               </Route>
               <Route exact path="/companyProfile">
                  <CompanyProfile />
               </Route>
               <Route exact path="/createJob">
                  <CreateJob />
               </Route>
               <Route path="/searchResults">
                  <SearchResults />
               </Route>
               <Route path="/jobApply">
                  <ApplyJob />
               </Route>
               <Route exact path="/jobs" render={props => <Auth><Jobs {...props} /></Auth>} />
               <Route exact path="/jobDetails/:id" render={props => <Auth><JobDetails {...props} /></Auth>} />
               <Route exact path="/homepage" render={props => <Auth><HomePage {...props} /></Auth>} />
               <Route exact path="/visumes" render={props => <Auth><Biographies {...props} /></Auth>} />
               <Route exact path="/createVisume" render={props => <Auth><CreateBiography {...props} /></Auth>} />
               <Route exact path="/profile" render={props => <Auth><ApplicantProfile {...props} /></Auth>} />
               <Route exact path="/myJobs" render={props => <Auth><AppliedAndSavedJobs {...props} /></Auth>} />
               <Route exact path="/applicantProfile/:id" render={props => <Auth><ApplicantProfileForCompany {...props} /></Auth>} />
            </Switch>
         </HashRouter>
      </ThemeProvider>

   );
}

export default App;
