import React from 'react';
//styles and icons 
import { Paper} from '@material-ui/core';
import {  Col} from 'react-bootstrap';
import educationIcon from '../../../images/education.svg';

// this component represent the profile Education part in explore opportunities page 
export default function Education(props){
    let educations = props.data;
        return (
            <React.Fragment>
                <Paper className="p-3 mt-4" style={{ borderTop: '2px solid #F27016', borderRadius: '0px' }}>
                    <header className="row m-0 mb-3 align-items-center">
                        <Col className="text-left p-0 ">
                            <h6 className="bold mb-0">
                                <span ><img src={educationIcon} className="pr-3" alt="icon" /></span>
                                Education</h6>
                        </Col>
                    </header>
                    {(educations) ?
                        educations.map(education =>
                            <main key={education.id} id={education.id} className="row m-0">
                                <Col xs={9} className="p-0">
                                    <h6 className="captialize bold">{education.title + ' at ' + education.at_name}</h6>
                                    <div><p className="text-grey mb-0">
                                        {education.start_time.substring(0,7)} - {(education.finish_time) ? education.finish_time.substring(0,7) : 'Present'}</p>
                                    </div>
                                </Col>
                                <Col md={9} className="p-0">
                                    <p>{(education.bio) ? education.bio : ''}</p>
                                </Col>
                                <Col xs={12} className="border-bottom mt-2 mb-2"></Col>
                            </main>)
                        : ''}
                </Paper>
            </React.Fragment>
        )
        
}