import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router';

// styles
import {  Container , Row , Col  , ProgressBar} from 'react-bootstrap';
import { Paper } from '@material-ui/core';

export default function ProfileStrength(props) {
    return(
        <Container fluid className="profileStrength p-0 pt-5 mb-3">
            <Paper>
                <Row className="m-0 p-3">
                    <Col xs={12} className="p-0 mb-2">Your profile strength is <span className="text-orange">83%</span></Col>
                    <Col xs={12} className="p-0 mb-2">
                        <ProgressBar  now='83' />
                    </Col>
                    <Col xs={12} className="p-0">
                        <Link className="underline text-orange" to="/companyProfile">Complete Your Profile</Link>
                    </Col>
                </Row>
            </Paper>
        </Container>
    )
}