import React, { useState} from 'react';
import { Link } from 'react-router-dom';

import Pagination from '../../pagination/pagination.component';
import CardVisume from './card.component';
import NoData from '../../no-data/no-data.component';

// styles , icons and images
import { Row, Col } from 'react-bootstrap';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { Paper } from '@material-ui/core';


// this component represents the videos container part in visumes/biographies page 

export default function Visumes(props){
        const [visumes , setVisuems] = useState(props.data.data);
        let userName = props.userName;
        if(!visumes.length > 0) {
            return (
                <div style={{ borderTop: '3px solid #e85a19' }}>
                    <div className="container-responsive">
                        <Paper elevation={3}>
                            <Row elevation={3} className="bg-light m-0 p-3" style={{ borderBottom: '1px solid #f2f2f2' }}>   
                                <NoData />         
                            </Row>
                        </Paper>
                    </div>
                </div>
            )
        }
        else {
            return (
                <div style={{ borderTop: '3px solid #e85a19' }}>
                    <div className="container-responsive">
                        <Paper elevation={3}>
                            <Row elevation={3} className="bg-light m-0 p-3" style={{ borderBottom: '1px solid #f2f2f2' }}>
                                <Col sm={6} xs={12}>
                                    <h5 className="mb-0">{userName && userName}'s Visumes</h5>
                                </Col>
                                <Col className="text-right" sm={6} xs={12}>
                                    <h6 className="bold mb-0"
                                    >Sort by: <Link className="text-orange" to=''
                                        onClick={e => {
                                            e.preventDefault();
                                            console.log(visumes);
                                            if(visumes.length > 0){
                                                let temp = visumes.reverse();
                                                setVisuems(temp);
                                            }
                                        }
                                        }>Latest</Link> <ExpandMoreIcon /></h6>
                                </Col>
                            </Row>
                            <Pagination card={visumes}>
                                <CardVisume />
                            </Pagination>
                        </Paper>
                    </div>
                </div>
                );
        }
}