import React, { Component } from 'react';
import {  Container , Row , Col  } from 'react-bootstrap';

import CradYourInterview from '../card-your-interview/card-your-interview.component';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import {Paper ,Button } from '@material-ui/core';
import { Link } from 'react-router-dom';
import noPostsFound from '../homepage/images/no-posts.svg';


export default class  YourInterviews extends Component{
    render(){
        return(<div>
            <Container className="pt-5">
                <Paper elevation={3}>
                    <Row elevation={3} className="bg-light m-0 p-3" style={{borderBottom:'1px solid #f2f2f2'}}>
                            <Col xs={6}>
                                <h5>Live Jobs</h5>
                            </Col>
                             {/* <Col className="text-right" xs={6}>
                                <h5><Link className="text-orange" to='/'>See All <ArrowForwardIcon /></Link></h5>
                            </Col> */}
                    </Row>
                    <Row className="m-0">
                        <Col md='12'  className="text-center pt-2 pb-5">
                            <div className="pt-3 pb-3">
                                <img src={noPostsFound} alt="No Posts" className="img-fluid"/>
                            </div>
                            <div>
                                <p className="text-grey">You've no Jobs yet</p>
                            </div>
                            <div>
                                <Link 
                                    onClick={e=>{
                                        window.scrollTo({ top: 0, behavior: 'smooth' })
                                        localStorage.setItem('updatedJobPost','')
                                        }} to="/createJob">
                                    <Button  size="large" varient="outlined" className="btn-orange-gradiant text-white pr-5 pl-5">Post Job</Button>
                                </Link>
                            </div>
                        </Col>
                        {/* <Col md='12' className="pt-3 pb-3" style={{borderBottom:'1px solid #f2f2f2'}}>
                            <CradYourInterview />
                        </Col>
                        <Col md='12' className="pt-3 pb-3" style={{borderBottom:'1px solid #f2f2f2'}}>
                            <CradYourInterview />
                        </Col>
                        <Col md='12' className="pt-3 pb-3" style={{borderBottom:'1px solid #f2f2f2'}}>
                            <CradYourInterview />
                        </Col> */}
                     </Row>
                </Paper>
            </Container>
        </div>);
    }
}