import React, { Component } from 'react';
import {  Row , Col, Container  } from 'react-bootstrap';
import ListItem from '../list-item/list-item.component';
import {Button, Paper} from '@material-ui/core';
import { Link } from 'react-router-dom';
import img from '../../images/member.png';

export default class  CompanyMembers extends Component{
    render(){
        return(
            <Container fluid className="p-0 mb-3">
                <Paper>
                    <Row className="m-0 p-3 border-b-1">
                            <Col className="pl-0 pb-3"  xs={7}>
                                <h6>Company Members</h6>
                            </Col>
                            <Col className="p-0 pb-3 text-right" xs={{ span : 4,offset : 1}}>
                                <h6><Link className="text-orange underline">View All</Link></h6>
                            </Col>
                            <Col className="p-0 mb-1" xs={8}>
                                <Row className="m-0 mb-3">
                                    <Col className="p-0" xs={4}><img src={img} width="100%" alt='list item img'/></Col>
                                    <Col  xs={8}>
                                        <h6>Alberto Mohe</h6>
                                        <p>web developer</p>
                                    </Col>
                                </Row>
                            </Col>
                            <Col className="p-0 text-right" xs={4}><Button className="btn-black" size="small" variant="outlined">Contact</Button></Col>
                            <Col className="p-0 mb-1" xs={8}>
                                <Row className="m-0 mb-3">
                                        <Col className="p-0" xs={4}><img src={img} width="100%" alt='list item img'/></Col>
                                        <Col  xs={8}>
                                            <h6>Alberto Mohe</h6>
                                            <p>web developer</p>
                                        </Col>
                                    </Row>
                            </Col>
                            <Col className="p-0 text-right" xs={4}><Button className="btn-black" size="small" variant="outlined">Contact</Button></Col>
                            <Col className="p-0 mb-1" xs={8}>
                                <Row className="m-0">
                                    <Col className="p-0" xs={4}><img src={img} width="100%" alt='list item img'/></Col>
                                    <Col  xs={8}>
                                        <h6>Alberto Mohe</h6>
                                        <p>web developer</p>
                                    </Col>
                                </Row>
                            </Col>
                            <Col className="p-0 text-right" xs={4}><Button className="btn-black" size="small" variant="outlined">Contact</Button></Col>
                    </Row>
                    </Paper>
            </Container>
        )
    }
}