    stopWebCamera = () => {
        var video = document.querySelector('video');
        const stream = video.srcObject;
        if (stream) {
            const tracks = stream.getVideoTracks();
            tracks[0].stop()
            tracks.forEach(function (track) {
                track.stop();
            });
            video.srcObject = null;
        }
    }

    componentWillUpdate(props) {
        if (props !== this.props) {
            this.setState({ ...this.state, bioId: props.data.id, data: props.data, enableWebCam: props.enableWebCam });
        }
    }
    maxtime = parseInt(this.props.data.maximum_time + "000");

